  methods: {
    details() {
      this.dialog = true;
    },
  },
  activated() {
    console.log("activated");
    setTimeout(() => {
      this.loading = false;
    }, 1000);
  },
  deactivated() {
    console.log("deactivated");
    this.loading = true;
